import React from 'react';
import {Link} from 'react-router-dom';
import './Listing.css';
import StarsRating from 'stars-rating';

const ratingChanged = (newRating) => {
    console.log(newRating)
  }

const ListingDisplay = (props) => {
     const renderList =({listdata})=>{
         if(listdata){
             return listdata.map((item)=>{
                 return(
                     <div className="item" key={item._id}>
                            <div className="row">
                                <div className="col-md-6">
                                    <img className="image" src={item.thumb}/>
                                </div>
                                <div className="col-md-6">
                                    <div className="hotel_name">
                                        <Link to={`/details/${item._id}`}>{item.name}</Link>
                                    </div>
                                    <div className="city_name"><b>{item.city_name}</b></div>
                                    <div className="city_name">{item.locality}</div>
                                    <div className="city_name">{item.address}</div>
                                        <br/>
                                        <br/>
                                        <br/>
                                   &nbsp;<Link to={`/details/${item._id}`}><button className="btn btn-info">Go for Order</button></Link> 
                                </div>
                            </div> 
                                          
                    <hr/>
                    <div className="row">
                            <div className="col-md-3">
                                <div className="cuisine_block">Cuisine</div>
                                <div className="cuisine_block">Cost per Two</div>
                            </div>
                            <div className="col-md-6">
                                <div className="cuisine_out">{item.Cuisine[0].name} || {item.Cuisine[1].name}</div>
                                <div className="cuisine_out">Rs. <b>{item.cost}</b></div> 
                           </div>
                           <div className="col-md-3">
                              <span className="ratingclass"><strong>Give Rating</strong></span>
                           <StarsRating
                                    count={5}
                                    onChange={ratingChanged}
                                    size={24}
                                    color2={'#ffd700'} />,                           
                           </div>
                
                     </div>
                   </div> 
                 )
             })
         }else{
             return(
                 <div>
                     <img src="/images/preloader.gif"/>
                 </div>
             )
         }
     }
     return(
         <div>
             <div className="row">
               
                 <div className="col-md-8">
                    {renderList(props)}
                 </div> 
                 
             </div> 
         </div>
     )
}
export default ListingDisplay;