import React, { Component } from 'react';
import axios from 'axios';

const costurl="https://edumatorestaurentapi.herokuapp.com/restaurantList";

class CostFilter extends Component{

    costFilter = (event) =>{
        let costvalue = (event.target.value).split(',');
        let lcost = Number(costvalue[0]);
        let hcost  = Number(costvalue[1]);
        let mealType = sessionStorage.getItem('mealid');

        let courl;
        if(costvalue == ''){
            courl = `${costurl}/${mealType}`
        }
        else{
            courl = `${costurl}/${mealType}?hcost=${lcost}&lcost=${hcost}`
        }
        axios.get(courl)
        .then((response)=>{
            this.props.costdata(response.data)
        })
    }
    
    render(){
        return(
            <React.Fragment>
            
                  <button className="btn btn-secondary"><b>Cost Filter Here </b></button>
                <div onChange={this.costFilter}>
                    <label className="radio">
                        <input type="radio" value="" name="filter"/>All
                    </label>
                    <label className="radio">
                        <input type="radio" value="100,300" name="filter"/>100 - 300
                    </label>
                    <label className="radio">
                        <input type="radio" value="301,500" name="filter"/>300 - 500
                    </label>
                    <label className="radio">
                        <input type="radio" value="501,1000" name="filter"/>500 - 1000
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default CostFilter;