import React, { Component } from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import { render } from '@testing-library/react';
import './order.css';

const resturl="https://edumatorestaurentapi.herokuapp.com/restaurantDetails";
const placeorder="https://edumatorestaurentapi.herokuapp.com/placeorder";

class Placeorder extends Component{
    constructor(){
    super()
    this.state={
        name:'',
        rest_name:'',
        order_id:Math.floor(Math.random()*1000),
        phone:'',
        person:''
    }
}
handleChangePerson = (event)=>{
    this.setState({person:event.target.value})
}
handleChangePhone =(event)=>{
    this.setState({phone:event.target.value})
}
handleChangeName=(event)=>{
    this.setState({name:event.target.value})
}
handleSubmit=()=>{
    fetch(placeorder,{
        method:'POST',
        headers:{
            'Accept':'application/json',
            'content-Type':'application/json'
        },
        body:JSON.stringify(this.state)
    })
    .then((this.props.history.push('/viewBooking?message=success')))
}
async componentDidMount(){
    let restid = this.props.match.params.id;
    let response = await axios.get(`${resturl}/${restid}`);
    this.setState({rest_name:response.data[0].name})
}
render(){
        return(
            <div className="container">
                <div className="panel panel-info">
                    <div className="panel-heading">
                        <h4>Place your order</h4>
                    </div>               
                     <div className="panel-body formcss">
                        <div className="form-group">
                            <label>Oredr Id</label>
                            <input type="text" name="order_id" value={this.state.order_id} 
                            readOnly className="form-control"/> 
                        </div>
                        <div className="form-group">
                            <label>Restaurent name</label>
                            <input type="text" name="rest_name" value={this.state.rest_name} 
                            readOnly className="form-control"/> 
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" name="name" value={this.state.name} 
                             className="form-control" onChange={this.handleChangeName}/> 
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input type="text" name="phone" value={this.state.phone} 
                             className="form-control" onChange={this.handleChangePhone}/> 
                        </div>
                        <div className="form-group">
                            <label>Person</label> 
                            <select type="text" name="person" value={this.state.person} 
                            className="form-control" onChange={this.handleChangePerson}>
                                <option value="1">One</option>
                                <option value="2">Two</option>
                                <option value="3">Three</option>
                            </select>
                        </div>
                        <div id="myModal" class="modal fade" role="dialog">
	                        <div class="modal-dialog">		  
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                                            <h4 class="modal-title">Hey..! you are Just one step away </h4>
                                        </div>
                                        <div class="modal-body">
                                                <p>Press the <button className="btn btn-success"> Submit Form </button> for order the food</p>
                                        </div>
                                        <div class="modal-footer">
                                            <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                                        </div>
                                    </div>
	                        </div>
                        </div>
                         
                        <Link to={`/details/${this.props.match.params.id}`} className="btn btn-danger">Back</Link>&nbsp;
                        <button className="btn btn-success" data-toggle="modal" data-target="#myModal">Place Order</button>&nbsp;
                        <button className="btn btn-success" onClick={this.handleSubmit}>Submit Form</button>
                     </div>
                </div>
                
            </div>
        )
 }
}
export default Placeorder;