
import {Link} from 'react-router-dom';
import './Header.css';
import { ThemeProvider } from 'styled-components';
import { lightTheme, darkTheme } from './theme';
import { GlobalStyles } from './global';
import React, { useState } from 'react';

const Header = () =>{
    
    const [theme, setTheme] = useState('light');

// The function that toggles between themes
const toggleTheme = () => {
  // if the theme is not light, then set it to dark
  if (theme === 'light') {
    setTheme('dark');
  // otherwise, it should be light
  } else {
    setTheme('light');
  }
}
    return(
        <React.Fragment>
          <div className="containersecond"> 
           <nav className="navbar navbar-inverse">
               <div className="container-fluid">
                   <div className="navbar-header">
                   
                <Link className="navbar-brand" to="/">EduMato  <span className="glyphicon glyphicon-cutlery"></span></Link>
                   </div>
                   <div className="collapse navbar-collapse" id="mynavbar">
                       <ui className="nav navbar-nav">
                           <li><Link to="/">Home</Link></li> 
                           <li><Link to="/viewBooking">Orders</Link></li> 
                           <li></li>
                                                      
                       </ui>  
                       <ui className="nav navbar-nav navbar-right iconcolor">
                           <li><button className="btn btn-danger btn-navbar"> <span className="glyphicon glyphicon-adjust" onClick={toggleTheme}></span></button></li>                            
                         </ui>                     
                   </div>                  
               </div>               
           </nav>  
          </div> 
          <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}>
                                    <>
                                      <GlobalStyles />                                    
                                      </>
                                      </ThemeProvider>
        </React.Fragment>
    )
}
export default Header;
