import React,{Component} from 'react';
import './Search.css';
import {withRouter} from 'react-router-dom';

const lurl = "https://edumatorestaurentapi.herokuapp.com/location";
const rurl = "https://edumatorestaurentapi.herokuapp.com/restaurant?city="

class Search extends Component{
    constructor(){
        super()

        this.state={
            location:'',
            restaurents:''
        }

    }

    //Display the city/Location data 

    renderCity = (data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value={item._id}>
                        {item.city_name} 
                    </option>
                )
            })
        }
    }   

    //Display the restaurent data if data is comming
    renderRestaurent = (data) =>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id}>
                        {item.name} | {item.locality}
                    </option>
                )
            })
        }
    }
    

    //Display the restaurent list with respect to city

    handleCity = (event)=>{
        const cityid = event.target.value;
        const resturl = `${rurl}${cityid}`;
        fetch(resturl,{Method:'GET'})
        .then((res)=> res.json())
        .then((data)=>{this.setState({restaurents:data})})
    }
   
    //Restaurent details from list of restaurent
    handleRest =(event)=>{
        this.props.history.push(`/details/${event.target.value}`)
    }

    render(){
        console.log(this.props);
        console.log(this.state.location);
        return(
            <div className="imageContainer">
                <div id="logo">
                    e!
                </div>
                <div className="heading">
                    Find the best restaurants,cafes, bars
                </div>
                <div className="locationSelector">
                    <select className="locationDropDown" onChange={this.handleCity}>
                        <option>----SELECT CITY---------</option>
                        {this.renderCity(this.state.location)}
                    </select>
                    <select className="locationDropDown" onChange={this.handleRest}>
                        <option>----SELECT RESTAURENTS---------</option>
                        {this.renderRestaurent(this.state.restaurents)}
                    </select>
                   
                </div>
            </div>
        )
    }

    //calling an city api
    componentDidMount(){
        fetch(lurl,{Method:'GET'})
        .then((res)=>res.json())
        .then((data) => {this.setState({location:data})})
        console.log(this.state.location);
    }
}
export default withRouter(Search);
