import React from 'react';
import {Link} from 'react-router-dom';
import './suggestion.css';

const ListingDisplay = (props)=>{
    const renderList = ({suggestion_data})=>{
        if(suggestion_data){
            return suggestion_data.map((item)=>{
                return(
                    <div className='card col-md-2'>
                         <h3><Link to={`/details/${item._id}`}><b>{item.name}</b></Link></h3>
                         <img className='card-img-top' src={item.thumb}/>
                                <div className='card-body'>
                        <h4 className='card-title'>{item.locality}</h4>
                            <p className="card-text">
                                 <p><b>Cost : {item.cost}</b></p>
                            </p>
                         </div>
                        <center>
                         <button className="btn btn-info button-text"><Link to={`/details/${item._id}`}>Go For Order</Link></button>
                       </center> 
                </div>
                )
            })
        }
    }
    return(
        <div className="container">
            <hr/>
            <h3>Thinking About Pocket on date {new Date().getDate()}. We have Pocket Friendly Suggestion for you...</h3>
            <div className="row">
                {renderList(props)}
            </div>
            
        </div>
    )
}
export default ListingDisplay;