import React, { Component } from 'react';
import axios from 'axios';

let sortingurl="https://edumatorestaurentapi.herokuapp.com/restaurantList";

class SortFilter extends Component{

    sortingfilter=(event)=>{
        let sortType=Number(event.target.value);
        let mealType=sessionStorage.getItem('mealid');
        let newurl;
        newurl=`${sortingurl}/${mealType}?sort=${sortType}`;
        console.log("hityrdf");
        console.log(newurl);
        axios.get(newurl)
        .then((response)=>{this.props.sortdata(response.data)})
    }
    render(){
        return(
            <React.Fragment>
                <button className="btn btn-secondary"><b>Sort By Cost</b></button>
                <div onChange={this.sortingfilter}>
                    <label className="radio">
                        <input type="radio" value="1" name="filter"/>Low to High
                    </label>
                    <label className="radio">
                        <input type="radio" value="-1" name="filter"/>High to Low
                    </label>
                </div>
            </React.Fragment>
        )
    }
}
export default SortFilter;