import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './ListingDisplay';
import CostFilter from '../filter/costfilter';
import CuisineFilter from '../filter/cuisinefilter';
import SortFilter from '../filter/sortfilter';
import SuggestionBlock from './SuggestionLogic';
import './Listing.css';

const url="https://edumatorestaurentapi.herokuapp.com/restaurant?mealtype=";
const mealurl="https://edumatorestaurentapi.herokuapp.com/mealtype";
class ListingApi extends Component{
    constructor(props){
        super()

        this.state={
            hotellist:'',
            mealname:''
        }
    }

    setDataByFilter(sorteddata){
        this.setState({hotellist:sorteddata})
    }

    render(){
        return(
            <div className="container listproper">
                <div className="row">
                
                    <div className="col-lg-2">
                      <center>
                         <CostFilter costdata={(data)=>{this.setDataByFilter(data)}}/>
                         <br/>
                         <br/>
                         <CuisineFilter cuisinedata={(data)=>{this.setDataByFilter(data)}}/>
                         <br/>
                         <br/>
                         <SortFilter sortdata={(data)=>{this.setDataByFilter(data)}}/>
                       </center>
                    </div>
                    
                    <div className="col-lg-9">
                        <SuggestionBlock/>
                    <center>
                        <h3>Here are the options of <b>{this.state.mealname}</b></h3>
                    </center>
                    <br/>
                    <ListingDisplay listdata={this.state.hotellist}/>
                     </div>
                     <div className="col-lg-1">
                        
                    </div>
                    
               </div> 
            </div>
        )
    }

    componentDidMount(){
        var mealid= this.props.match.params.id
        sessionStorage.setItem('mealid',mealid);
        axios.get(`${mealurl}`)
        .then((response)=>{
            for(var i=0;i<=response.data.length;i++){
                if(response.data[i]._id == Number(mealid)){
                    this.setState({mealname:(response.data[i].name).toUpperCase()})
                    i=response.data.length;
                
                }else{
                    this.setState({mealname:'Please enter valid mealid'})
                }
            }
        })
        axios.get(`${url}${mealid}`)
        .then((response)=>{
            this.setState({hotellist:response.data})
        })
    }
}
export default ListingApi;