import React, { Component } from 'react';
import axios from 'axios';
import SuggestionDisplay from './SuggestDisplay';

var suggestionurl = "https://edumatorestaurentapi.herokuapp.com/restaurantList";

class SuggestionBlock extends Component{
    constructor(){
        super()

        this.state={
            suggestion:''
        }
    }
    render(){
        return(
            <div>
                <SuggestionDisplay suggestion_data={this.state.suggestion}/>
            </div>
        )
    }

    componentDidMount(){
        let date = new Date().getDate();
        let mealid = sessionStorage.getItem('mealid');
        let sendurl;

        if(date>0 && date<11){
            sendurl=`${suggestionurl}/${mealid}?hcost=500&lcost=1000`;
        }else if(date>10 && date<21){
            sendurl=`${suggestionurl}/${mealid}?hcost=300&lcost=800`;
        }else if(date>20 && date<32){
            sendurl=`${suggestionurl}/${mealid}?hcost=100&lcost=500`;
        }
   
    axios.get(sendurl)
    .then((response)=>{this.setState({suggestion:response.data})})
   }
 }

  export default SuggestionBlock;

